@import './reset.scss';


$메인칼라 : #ff0000;


.wow-enter{
    opacity: 0;
}
.wow-enter-active{
    opacity: 1;
    transition: all 500ms;
}


@mixin alert() {
    background-color: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.red {
    color : $메인칼라;
}

.my-alert {
    @include alert()
}
.my-alert2 {
    @extend .my-alert;
    background-color: #ffe591;
}


.my-alert p{
    margin-bottom: 0;
}
